import React, {useState} from 'react'
import styles from './Paginator.module.css'

let Paginator = (props) => {
   let [currentSegment, setCurrentSegment] = useState(0)
   
   let arrSegments = []
   let segmentSize = 20
   let totalPages = 156       // Почему не работает с props.pages  ?????
   let count = 1
   let sumSegments = Math.ceil(totalPages / segmentSize)      // Количество сегментов
         
   
   for(let i = 0; i < sumSegments; i++){            
      arrSegments[i] = []            
      for(let j = 0; j < segmentSize; j++){               
         arrSegments[i][j] = count ++
         console.log("totalPages = ", totalPages)
         if(count === totalPages + 1) break
      }         
   }
   
   let next = () => {
      setCurrentSegment(currentSegment + 1)
   }
   
   let prev = () => {
      setCurrentSegment(currentSegment - 1)
   }
   
   return (
      <div>
        <button onClick={prev}>Prev</button>
        
        {
            arrSegments[currentSegment].map((p) => {
              return <span 
                 key={p} 
                 className={p === props.currentPage ? styles.selectedPage : ''} 
                 onClick={(e) => props.handlerCurrentPage(p)}> {p} </span> 
            })
        
        }
        
        
         {/*props.pagesArr.map((p)=>{
            return <span key={p} className={p === props.currentPage ? styles.selectedPage : ''} onClick={(e) => props.handlerCurrentPage(p)}> {p} </span>
         })
         */}
         
         
         <button onClick={next}>Next</button>
      </div>
   )
}

export default Paginator